body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

* { 
  margin: 0px; 
  padding: 0px;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ----------------------------------------------------------------------------------------------- */
a {
  color: white; 
  text-decoration: none;
}

.box {
  border-radius: 10px;
  width: 100%;
  height: 100%;
  display: flex; 
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-image: linear-gradient(to bottom right, rgba(128, 128, 128, 0.8), rgba(255, 255, 255, 0.6));
}

.boxContainer {
  background-image: url("https://images.pexels.com/photos/73910/mars-mars-rover-space-travel-robot-73910.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500");
  background-repeat: no-repeat;
  background-size: cover; 
  margin: 30px; 
  width: 30vw;
  height: 30vh;
  border-radius: 10px;
  opacity: .8;
}

.box p {
  color: white;
}

.card {
  height: 100px !important; 
  width: 80%;
  background-color: white;
  /* background-image: linear-gradient(to bottom right, rgba(254, 252, 252, 0.995), rgba(255, 255, 255, 0.509)); */
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgb(106, 105, 105);
  padding: 10px;
  margin: 10px;
}

.card p {
  overflow: hidden;
  height: 50px;
}

.cardContainer {
  height: 85%;
  margin: 30px; 
  border-radius: 20px;
  display: flex; 
  flex-direction: column;
  align-items: center; 
  justify-content: center;
  overflow: scroll !important;
}

.container {
  background-color: rgb(0, 0, 0);
  background-repeat: no-repeat;
  background-size: cover; 
  width: 100vw;
  height: 100vh;
}

#closeModal {
  margin-left: 20px; 
  color: white;
}

.exp {
  width: 40%;
}

.exp button {
  margin-top: 20px; ;
}

.formContainer {
  color: white;
}

.leftColumn, .rightColumn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height:100%;
}

.links {
  width: 60vw; 
  display: flex; 
  align-items: center; 
  justify-content: space-evenly;
}

.mainContent {
  display: flex;
  justify-content: space-evenly;
  height: 90%;
}

.mainInfo {
  height:80vh;
  width: 50vw;
  color: white;
  display: flex; 
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-right: 20px;
}

.message {
  background-color: rgba(28, 27, 27, 0.845);
  padding: 50px;
  margin: 20px;
  color: white;
  border-radius: 10px;
  width: 30vw; 
}

.message2 {
  background-color: rgba(28, 27, 27, 0.845);
  padding: 50px;
  margin: 20px;
  color: rgb(255, 255, 255);
  border-radius: 10px;
  width: 30vw;
  height: 40vh;
  overflow: scroll;
}

::-webkit-scrollbar {
  display: none;
}


.navBar {
  background-color: rgba(0, 0, 0, 0.351);
  width: 100vw;
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: space-between; 
}

.navBar h1 {
  padding: 5px;
  margin-left: 50px;
  color: white;
}

.newsContainer {
  height: 100%;
  text-align: center;
  padding: 20px; 
}

.newsContainer h2 {
  color: white;
  margin-top: 10px;
}

#openModal, #closeModal {
  cursor: pointer;
}

.planetBox {
  color: white;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.planetPhoto img {
  max-height: 50vh;
  max-width: 50vw;
}

.planetInfo {
  font-size: larger;
}

.pic img {
  max-height: 40vh;
  max-width: 30vw;
  margin: 10px; 
  border-radius: 10px;
  
}

.picContainer { 
  padding: 15px;  
  text-align: center; 
  color: white;
  border-radius: 10px;
}

.picContainer h2 {
  margin: 10px; 
}

.picContainer button {
  color: white;
  background-color: rgba(50, 48, 48, 0.461); 
  border: none; 
}

#photoModal {
  display: flex;
  height: 80vh;
  width: 80vw;
	z-index: 6;
	border-radius: 5px;
	top: 100px; 
	position: fixed;
	background-color: rgba(0, 0, 0, 0.988);
	border-radius: 25px;
	padding: 10px;
	box-shadow: 6px 6px 20px rgba(122, 122, 122, 0.5);
}

#photoModalDetails {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

#photoModalDetails img {
  max-height: 80%;
  max-width: 50%;
}


